import React, { Component } from 'react'
import { Row, Col } from 'antd'

import './index.css'

export default class Skill extends Component {
  render() {
    return (
      <div className="full-high">
        <Row className="title-high">
          <Col span={24} className="full-high txt large-txt padt"></Col>
        </Row>
        <Row type="flex" justify="center" align="middle" className="cont-high padc">
          <Col span={4} className="full-high"></Col>
          <Col span={16} className="full-high">
            <div className="txt">htmlcssJavaScriptJQuery</div>
            <div className="txt">ES6lessAnt Design UI</div>
            <div className="txt">ReactReact-Routerredux</div>
            <div className="txt">
              npmyarnwebpackGitSVN
            </div>
            <div className="txt">AMDrequire.jscommonJSES6 module</div>       
            <div className="txt">HTTP</div>
            <div className="txt">Hybrid App</div>
            <div className="txt">Node.js</div>
            <div className="img-skill skill2"></div>
          </Col>
          <Col span={4} className="full-high"></Col>
        </Row>
      </div>
    );
  }
}



